//Variables
$primary-color: #2e3641;
$secondary-color: #dae4eb;
$taskDone-color: #9eb2c0;
$verticalLine-color: #454f5c;
$verticalLine2-color: #f2e3df;

//Mixins
@mixin icon ($color){
  width: 10px;
  height: 10px;
  color: $color;
}

@mixin verticalLine ($height, $bg, $left){
  content: '';
  position: absolute;
  height: $height;
  width: 1px;
  background: $bg;
  top: 0;
  left: $left;
}

/*General*/
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Lato', sans-serif;
  background: #e9f0f4;
}

/*Remove the default button style*/
button {
  background: none;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
}

/*Remove the default input text style*/
.toDoList-input {
  height: 27px;
  border: none;
  outline: none;
  font-family: 'Lato', sans-serif;
  font-size: 12px;
  color: $primary-color;
  margin-left: 20px;
}

/*Style the toDoList*/
/*Container*/
.toDoList-app {
  width: 320px;
  margin: 40px auto;
}

/*Header*/
.toDoList-header {
  height: 36px;
  background: $primary-color;
  display: flex;
  align-items: center;
  position: relative;
}

.toDoList-title {
  color: $secondary-color;
  font-weight: 700;
  font-size: 14px;
  margin-left: 43px;
}

/*ToDoList and toDoItem*/
.toDoList-content {
  background: #fff;
  border: 1px solid #dbdbdb;
  border-top: none;
}

.toDoItem {
  height: 30px;
  list-style: none;
  border-bottom: 1px solid #e6ebed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

//If task is done
.done{
  color: $taskDone-color;
  text-decoration: line-through;
}

.done i {
  color: $taskDone-color;
}

//If task is undone
.undone {
  color: $primary-color;
}

//Create a custom checkbox
.toDoItem label {
  display: block;
  position: relative;
  padding-left: 42px;
  user-select: none;
  font-size: 12px;
}

.toDoItem input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  top: 2px;
  left: 0;
  height: 10px;
  width: 10px;
  background-color: #fafbfc;
  border: 1px solid $secondary-color;
  margin-left: 10px;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.toDoItem label input:checked ~ .checkmark:after {
  display: block;
}

.toDoItem label .checkmark:after {
  left: 2px;
  bottom: 2px;
  width: 3px;
  height: 5px;
  border: solid #a8bd44;
  border-width: 0 1px 1px 0;
  transform: rotate(45deg);
}

//Create a vertical lines
.toDoList-header::before {
  @include verticalLine (36px, $verticalLine-color, 29px);
}

.toDoList-header::after {
  @include verticalLine (36px, $verticalLine-color, 32px);
}

.toDoItem::before {
  @include verticalLine (29px, $verticalLine2-color, 28px);
}

.toDoItem::after {
  @include verticalLine (29px, $verticalLine2-color, 31px);
}

.toDoList-add {
  position: relative;
}

.toDoList-add::before {
  @include verticalLine (29px, $verticalLine2-color, 28px);
}

.toDoList-add::after {
  @include verticalLine (29px, $verticalLine2-color, 31px);
}


/*/*Style the icons*/
.fa-trash-alt {
  object-fit: contain;
  margin-right: 12px;
  @include icon($primary-color);
}

.fa-plus {
  margin-left: 10px;
  @include icon($secondary-color);
}




